.Page--transition {
  -webkit-transition: opacity 0.5s cubic-bezier(0.2, 0, 0.57, 1), -webkit-transform 0.3s cubic-bezier(0.2, 0, 0.57, 1);
  transition: opacity 0.5s cubic-bezier(0.2, 0, 0.57, 1), -webkit-transform 0.3s cubic-bezier(0.2, 0, 0.57, 1);
  -o-transition: opacity 0.5s cubic-bezier(0.2, 0, 0.57, 1), transform 0.3s cubic-bezier(0.2, 0, 0.57, 1);
  transition: opacity 0.5s cubic-bezier(0.2, 0, 0.57, 1), transform 0.3s cubic-bezier(0.2, 0, 0.57, 1);
  transition: opacity 0.5s cubic-bezier(0.2, 0, 0.57, 1), transform 0.3s cubic-bezier(0.2, 0, 0.57, 1), -webkit-transform 0.3s cubic-bezier(0.2, 0, 0.57, 1);
}
.Page--transitionentering {
  opacity: 0;
  -webkit-transform: translateY(50px) scale(1.1);
      -ms-transform: translateY(50px) scale(1.1);
          transform: translateY(50px) scale(1.1);
}
.Page--transitionentered {
  /*transition-delay: 0.2s;*/
}
.Page--transitionexiting {
  -webkit-transition-duration: 0.2s;
       -o-transition-duration: 0.2s;
          transition-duration: 0.2s;
  opacity: 0;
  -webkit-transform: translateX(-50px) scale(0.7);
      -ms-transform: translateX(-50px) scale(0.7);
          transform: translateX(-50px) scale(0.7);
}
.Page--transitionexited {
  pointer-events: none;
  display: none;
  -webkit-transform: translateY(50px) scale(1.1);
      -ms-transform: translateY(50px) scale(1.1);
          transform: translateY(50px) scale(1.1);
}



