.Icon__div {
  border-radius: 50%;
  background-color: white;
  width: 45px;
  height: 45px;
  padding: 8px;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: 1px solid rgba(77,77,77, 0.2);
  -webkit-transition: opacity cubic-bezier(0.2, 0, 0.57, 1) 0.3s, -webkit-box-shadow cubic-bezier(0.2, 0, 0.57, 1) 0.5s, -webkit-transform cubic-bezier(0.2, 0, 0.57, 1) 0.5s;
  transition: opacity cubic-bezier(0.2, 0, 0.57, 1) 0.3s, -webkit-box-shadow cubic-bezier(0.2, 0, 0.57, 1) 0.5s, -webkit-transform cubic-bezier(0.2, 0, 0.57, 1) 0.5s;
  -o-transition: box-shadow cubic-bezier(0.2, 0, 0.57, 1) 0.5s, transform cubic-bezier(0.2, 0, 0.57, 1) 0.5s, opacity cubic-bezier(0.2, 0, 0.57, 1) 0.3s;
  transition: box-shadow cubic-bezier(0.2, 0, 0.57, 1) 0.5s, transform cubic-bezier(0.2, 0, 0.57, 1) 0.5s, opacity cubic-bezier(0.2, 0, 0.57, 1) 0.3s;
  transition: box-shadow cubic-bezier(0.2, 0, 0.57, 1) 0.5s, transform cubic-bezier(0.2, 0, 0.57, 1) 0.5s, opacity cubic-bezier(0.2, 0, 0.57, 1) 0.3s, -webkit-box-shadow cubic-bezier(0.2, 0, 0.57, 1) 0.5s, -webkit-transform cubic-bezier(0.2, 0, 0.57, 1) 0.5s;
}
.Icon__div:hover {
  -webkit-box-shadow: 0px 0px 10px 0px rgba(77, 77, 77, 0.5);
          box-shadow: 0px 0px 10px 0px rgba(77, 77, 77, 0.5);
  -webkit-transform: scale(1.04);
      -ms-transform: scale(1.04);
          transform: scale(1.04);
}

.Icon__div--hide {
  -webkit-transform: scale(0.7) translate(50px, 50px);
      -ms-transform: scale(0.7) translate(50px, 50px);
          transform: scale(0.7) translate(50px, 50px);
  opacity: 0;
}

.Icon__img {
  width: 100%;
  height: 100%;
}

.Icon__innerDiv {
  margin-top: 8px;
  font-size: 6px;
  color: #555;
  text-align: center;
  opacity: 0;
  -webkit-transform: translateY(-6px);
      -ms-transform: translateY(-6px);
          transform: translateY(-6px);
}

.Icon__innerDiv--active {
  -webkit-transition: opacity 0.5s ease, -webkit-transform 0.5s ease;
  transition: opacity 0.5s ease, -webkit-transform 0.5s ease;
  -o-transition: transform 0.5s ease, opacity 0.5s ease;
  transition: transform 0.5s ease, opacity 0.5s ease;
  transition: transform 0.5s ease, opacity 0.5s ease, -webkit-transform 0.5s ease;
  -webkit-transition-delay: 0.5s;
       -o-transition-delay: 0.5s;
          transition-delay: 0.5s;
  opacity: 1;
  -webkit-transform: translate(0, 0);
      -ms-transform: translate(0, 0);
          transform: translate(0, 0);
}

.Icon__innerDiv--active_1 {
  -webkit-transition-delay: 0.6s;
       -o-transition-delay: 0.6s;
          transition-delay: 0.6s;
}
.Icon__innerDiv--active_2 {
  -webkit-transition-delay: 0.8s;
       -o-transition-delay: 0.8s;
          transition-delay: 0.8s;
}
.Icon__innerDiv--active_3 {
  -webkit-transition-delay: 1.0s;
       -o-transition-delay: 1.0s;
          transition-delay: 1.0s;
}
.Icon__innerDiv--active_4 {
  -webkit-transition-delay: 1.2s;
       -o-transition-delay: 1.2s;
          transition-delay: 1.2s;
}
.Icon__div--active {
  border-width: 3px;
}



