.MoveAround {
  -webkit-transition: -webkit-transform cubic-bezier(0.2, 0, 0.57, 1) 0.5s;
  transition: -webkit-transform cubic-bezier(0.2, 0, 0.57, 1) 0.5s;
  -o-transition: transform cubic-bezier(0.2, 0, 0.57, 1) 0.5s;
  transition: transform cubic-bezier(0.2, 0, 0.57, 1) 0.5s;
  transition: transform cubic-bezier(0.2, 0, 0.57, 1) 0.5s, -webkit-transform cubic-bezier(0.2, 0, 0.57, 1) 0.5s;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 5;
}



